.wp-block-myblocks-adinfo-hero {
	margin: 0 !important;
	padding: 0 !important;
	margin-block-start: 0 !important;
	margin-block-end: 0 !important;
}

.adinfo-hero-render {
	margin: 0 !important;
	padding: 0 !important;

	.splide__track {
		position: relative;
	}

	.splide__pagination {
		position: absolute;
		bottom: 1rem;
		left: 50%;
		transform: translateX(-50%);
		z-index: 10;
		display: flex;
		gap: 0.3rem;
	}
}

.adinfo-hero {
	.components-button:not(:disabled, [aria-disabled="false"]),
	.components-button[aria-expanded="false"] {
		color: white;
	}

	.components-button:hover:not(:disabled, [aria-disabled="true"]),
	.components-button[aria-expanded="true"] {
		color: #0066cc;
	}

	.splide__slide {
		width: 100%;
		height: 680px !important;
	}

	/* Dot (bullet) style for Splide pagination */
	.splide__pagination__page {
		width: 22px !important;
		height: 22px !important;
		background-color: white !important;
		border-radius: 50% !important;
		box-shadow: 0 0 10px rgba(0, 0, 0, 0.8) !important;
		opacity: 1 !important;
		border: 2px solid white !important;
		background-color: transparent !important;

		&:hover {
			background-color: white !important;
			transition: background-color 0.3s ease !important;
			width: 24px !important;
			height: 24px !important;
		}
	}

	.splide__pagination__page.is-active {
		background-color: white !important;
		box-shadow: 0 0 10px rgba(0, 0, 0, 1) !important;
	}
}
